import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Modules from '../views/Modules.vue'
import Partnership from '../views/Partnership.vue'
import Media from '../views/Media.vue'
import Contact from '../views/Contact.vue'

Vue.use(VueRouter)

const routes = [
  { path: '/', name: 'Home', component: Home, meta: { layout: 'main-layout', title: 'Программа для аптеки Интайла', description: 'Облачная программа для аптеки Интайла, открыть аптеку' } },
  { path: '/modules', name: 'Modules', component: Modules, meta: { layout: 'main-layout', title: 'Модули программы для аптеки Интайла', description: 'Модули и возможности программы для аптеки Интайла, открыть аптеку' } },
  { path: '/partnership', name: 'Partnership', component: Partnership, meta: { layout: 'main-layout', title: 'Сотрудничество по программе для аптеки Интайла', description: 'Мы готовы сотрудничать с аптеками, производителями, дистрибьюторами' } },
  { path: '/media', name: 'Media', component: Media, meta: { layout: 'main-layout', title: 'Скриншоты и видео программы для аптек Интайла', description: 'Видеообзор и скриншоты программы для аптеки' } },
  { path: '/contact', name: 'Contact', component: Contact, meta: { layout: 'main-layout', title: 'Связаться для установки программы для аптеки', description: 'Контакты, телефон, адрес для установки облачной программы для аптек' } }
]

const router = new VueRouter({
	scrollBehavior () {
	return { x: 0, y: 0 }
	},
	mode: 'history',
	base: process.env.BASE_URL,
	routes
})

 router.beforeEach((to, from, next) => {
	const nearestWithTitle = to.matched.slice().reverse().find(r => r.meta && r.meta.title && r.meta.description)
	var meta = document.getElementsByTagName('meta')
	for (var i = 0; i < meta.length; i++) {
		if (meta[i].name.toLowerCase() === 'description') {
			meta[i].remove()
			}
	}
	if (nearestWithTitle) {
			document.title = nearestWithTitle.meta.title
			var description = document.createElement('meta')
			description.name = 'description'
			description.content = nearestWithTitle.meta.description
			document.head.appendChild(description)
	}
	next()
})

export default router
